import style from "./StaffCalender.module.css";
import {
  dayName,
  getCompareToday,
  useCalendar,
} from "../../Hooks/CustomHooks/use-calendar";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
const StaffCalender = () => {
  const { selectedDate, getPreviousMonth, getNextMonth, generateCalendar } =
    useCalendar();
  const onDateChoose = () => {};

  const onPrev = () => {
    getPreviousMonth();
  };
  const onNext = () => {
    getNextMonth();
  };
  //   console.info(
  //     "generateCalendar",
  //     getCompareToday(selectedDate, true),
  //     selectedDate
  //   );
  return (
    <div className={style.calendar}>
      <div className={style.calendar_top}>
        <img
          src={`${assestsUtilityUrl}/next.svg`}
          alt="previous icon"
          onClick={onPrev}
          style={{
            rotate: "180deg",
          }}
        />
        <h6>
          <span>{dayjs(selectedDate).format("MMMM")}</span>{" "}
          <span>{dayjs(selectedDate).format("YYYY")}</span>
        </h6>
        <img
          src={`${assestsUtilityUrl}/next.svg`}
          alt="next icon"
          onClick={onNext}
        />
      </div>

      <div>
        <div className={style.calendar_days}>
          {dayName.map((name) => (
            <p className={style.calendar_days_label} key={name}>
              {name}
            </p>
          ))}
        </div>
        {Object.values(generateCalendar)?.map((cols, index) => (
          <div className={style.calendar_days} key={index}>
            {cols.map((cl) => (
              <p
                className={style.calendar_days_value}
                key={cl?.date}
                style={{
                  "--calendar-border": getCompareToday(cl?.date)
                    ? "#420789"
                    : "",
                }}
                onClick={() => onDateChoose(cl?.date)}
              >
                {cl?.value}
                <div
                  className={style.calendar_dot}
                  style={
                    {
                      // for leave
                      // "--dot-color": "#00308F",
                      // for holiday
                      // "--dot-color": "#cacaca",
                      // for absent
                      // "--dot-color": "#ee3833",
                      //   for present
                      // "--dot-color": "rgba(37, 172, 74, 0.8)",
                    }
                  }
                ></div>
              </p>
            ))}
          </div>
        ))}
      </div>
    </div>
  );
};

export default StaffCalender;
